
import { Select, Input, Button, Table, Modal } from 'antd';
const { TextArea } = Input;
import { axios } from '&'
const { Option } = Select;
import { connect } from 'react-redux'
import './index.scss'
function setdata(res) {
    var data = [];
    res.data.result.forEach((item, index) => {
        var obj = {}
        obj.key = index + 1
        obj.i = index + 1
        obj.type = item.type
        obj.username = item.username
        obj.projectName = item.projectName
        obj.projectUrl = item.projectUrl
        obj.readme = item.readme
        obj.tech = item.tech
        obj.score = item.score ? item.score : ''
        obj.uid = item.uid
        obj._id = item._id
        data.push(obj)
    })
    return data
}
var style={
    first:{
        fontWeight:'bold',
        marginBottom:7
    },
    second:{
        marginBottom:20
    }
}

@connect(
    state => {
        return {
            user: state.getIn(['user', 'user'])
        }
    }
)
export default class Showpro extends Component {
    constructor() {
        super()
        this.state = {
            data: [],
            visible: false,
            one:'',
            two:'',
            three:"",
            four:"",
            _id:"",
            uid:''
        }
    }
    componentDidMount() {
        setTimeout(() => {
            axios.get('/react/getcore', { params: { _id: this.props.user._id } }).then(res => {
                var data = setdata(res)
                this.setState({
                    data,
                })
            })
        }, 300)
    }
    del = (record) => {
        var that = this
        Modal.confirm({
            title: '删除警告',
            content: '你确定要删除吗？',
            okText: '确认',
            cancelText: '取消',
            onOk() {
                axios.post('/react/delscore', { _id: record._id }).then(res => {
                    axios.get('/react/getcore', { params: { _id: record.uid } }).then(res => {
                        var data = setdata(res)
                        that.setState({
                            data,
                        })
                    })
                })
            }
        })
    }
    changeurl = ({ target: { value } }) => {
        this.setState({
            one: value
        });
    };
    changename = ({ target: { value } }) => {
        this.setState({
            two: value
        });
    };
    onChangetch = ({ target: { value } }) => {
        this.setState({
            three: value
        });
    };
    onChangereadme = ({ target: { value } }) => {
        this.setState({
            four: value
        });
    };
    change = (record) => {
        this.setState({
            visible: true,
            one:this.state.data[record.key-1].projectUrl,
            two:this.state.data[record.key-1].projectName,
            three:this.state.data[record.key-1].readme,
            four:this.state.data[record.key-1].tech,
            _id:record._id,
            uid:record.uid
        });
    };

    hideModal = () => {
        this.setState({
            visible: false,
        });
    };

    changepro=()=>{
        axios.post('/react/changescore',{
            projectUrl:this.state.one,
            projectName:this.state.two,
            readme:this.state.three,
            tech:this.state.four,
            _id:this.state._id
        }).then(res=>{
            axios.get('/react/getcore', { params: { _id: this.state.uid } }).then(res => {
                var data = setdata(res)
                this.setState({
                    data,
                    visible:false
                })
            })
        })
    }
    render() {
        var columns = [
            {
                title: '序号',
                dataIndex: 'i',
                key: 'i',
                width: 70,
                align: 'center'
            },
            {
                title: '框架',
                dataIndex: 'type',
                key: 'type',
                width: 100,
                align: 'center'
            },
            {
                title: '项目名称',
                dataIndex: 'projectName',
                key: 'projectName',
                width: 120,
                align: 'center',
            },
            {
                title: '项目地址',
                dataIndex: 'projectUrl',
                key: 'projectUrl',
                width: 150,
                align: 'center',
                textWrap: ' word-break',

            },
            {
                title: '分数',
                dataIndex: 'score',
                key: 'score',
                width: 80,
                align: 'center'
            },
            {
                title: '操作',
                dataIndex: 'action',
                key: 'action',
                width: 100,
                align: 'center',
                render: (text, record) => <span style={{ display: 'flex', justifyContent: "space-around" }} >
                    <a value='2222' onClick={() => this.del(record)}>删除</a>
                    <a onClick={() => this.change(record)}>修改</a>
                </span>
            }]
        return (
            <div>
                <h2>个人项目</h2>
                <Table columns={columns} dataSource={this.state.data} bordered={true} />
                <Modal
                    title="Modal"
                    visible={this.state.visible}
                    onOk={this.changepro}
                    onCancel={this.hideModal}
                    okText="确认"
                    cancelText="取消"
                    centered={true}
                >
                    <div style={style.second}>
                        <div style={style.first}>项目地址：</div>
                        <Input placeholder="项目上线地址" allowClear style={{ width: 400 }} value={this.state.one} onChange={this.changeurl}/>
                    </div>
                    <div style={style.second}>
                        <div style={style.first}>项目名称：</div>
                        <Input placeholder="项目名称" allowClear style={{ width: 400 }} value={this.state.two} onChange={this.changename}/>
                    </div>
                    <div style={style.second}>
                        <div style={style.first}>技术亮点：</div>
                        <TextArea
                            value={this.state.three}
                            onChange={this.onChangetch}
                            style={{ width: 400 }}
                            placeholder="请至少填写三条技术亮点"
                            autosize={{ minRows: 4, maxRows: 7 }}
                        />
                    </div>
                    <div style={style.second}>
                        <div style={style.first}>备注：</div>
                        <TextArea
                            value={this.state.four}
                            onChange={this.onChangereadme}
                            placeholder="请输入注册号的账号以及其他备注"
                            autosize={{ minRows: 4, maxRows: 7 }}
                            style={{ width: 400 }}
                        />
                    </div>
                </Modal>
            </div>
        )
    }
}